#modal-alert {
  position: fixed;
  width: 100%;
  height: 100%;
  min-width: 100vw;
  min-height: 100vh;

  transition: opacity 200ms;
  visibility: hidden;
  opacity: 0;
  &.opacity {
    opacity: 1;
  }
  &.visible {
    visibility: visible;
  }

  & .modal-alert-backdrop {
    height: 100%;
    width: 100%;
    background-color: #000;
    opacity: 0.6;
    z-index: 9000;
  }

  & .modal-alert-container {
    z-index: 999;
    position: fixed;    
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    
    padding: 8px 8px;
    border-radius: 4px;
    border: 1px solid black;
    box-shadow: 4px 4px 16px #444;
    background-color: #fff;
  }
}