@import "./index.scss"; 
@import './modal_alert.scss';


// object-fit
$fits: contain cover fill scale-down none;
@each $fit in $fits { 
  .fit-#{$fit} { object-fit: $fit; }
}


// overflow
$overflows: visible hidden scroll overlay auto;
@each $overflow in $overflows { 
  .overflow-#{$overflow} { overflow: $overflow; }
}
.fit-scroll-x {
  overflow: auto hidden;
}
.fit-scroll-y {
  overflow: hidden auto;
}




